// 
const BASE_URL = 'http://api-hmugo-web.itheima.net/api/public/v1'

// +1-10
let requestTime = 0

// request
export const request = (option) => {
	requestTime++
	uni.showLoading({
		title: ''
	})
	// Promiseresolve, reject
	return new Promise((resolve, reject) => {
		// API
		uni.request({
			// 
			url: BASE_URL + option.url,
			// GET
			method: option.method || 'GET',
			// 
			data: option.data || {},
			// 
			success: (res) => {
				// data.meta.status200
				if(res.data.meta.status !== 200) {
					uni.showToast({
						title: '',
						icon: "error"
					})
				}
				// resolve
				resolve(res)
			},
			// 
			fail: (err) => {
				uni.showToast({
					icon: "error",
					title: ''
				})
				reject(err)
			},
			// 
			complete: () => {
				requestTime--
				if(requestTime === 0) {
					uni.hideLoading()
					uni.stopPullDownRefresh()
				}
			}
		})
	})
}