import {request} from '@/util/request.js'

/**
 * 
 */
export const getSwiperAPI = () => {
	return request({
		url: '/home/swiperdata'
	})
}

/**
 * 
 */
export const getCatitemAPI = () => {
	return request({
		url: '/home/catitems'
	})
}

/**
 * 
 */
export const getFloorAPI = () => {
	return request({
		url: '/home/floordata'
	})
}

/**
 * 
 */
export const getSearchListAPI = (query) => {
	return request({
		url: '/goods/qsearch',
		data: {
			query
		}
	})
}

/**
 * 
 */
export const getCategoryListAPI = () => {
	return request({
		url: '/categories'
	})
}

/**
 * 
 * query,cid,pagenum,pagesize di
 */
export const getGoodsListAPI = ({query, cid, pagenum, pagesize}) => {
	return request({
		url: '/goods/search',
		data: {
			query,
			cid,
			pagenum,
			pagesize
		}
	})
}

/**
 * 
 * goods_id id
 */
export const getGoodsDetailAPI = (goods_id) => {
	return request({
		url: '/goods/detail',
		data: {
			goods_id
		}
	})
}